import React,{useState} from 'react'
import Modal from '../Modal/Modal'

const Login = (props) => {

    const [formData, setFormData] = useState({
        email: "",
        password: "",
      });
    
    const { email, password } = formData;

    const onChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const onSubmit = (e) => {
        e.preventDefault();
        props.HandleAdmin(email, password);
        props.Login();
      };

  return (
    <div >
        {props.HandleLogin ? (
        <div onClick={() => props.setHandleLogin()} className="back-drop" />
      ) : null}
      <Modal className="modal" show={props.HandleLogin}>
          <h1>LOGIN</h1>
          <form onSubmit={(e) => onSubmit(e)}>
              <div className='form-group'>
                  <input
                    type="text"
                    placeholder="Email"
                    value={email}
                    name="email"
                    onChange={(e) => onChange(e)}
                    className='custom-input'
                    />
              </div>
              <div className='form-group'>
                  <input
                    type="password"
                    placeholder="Password"
                    value={password}
                    name="password"
                    onChange={(e) => onChange(e)}
                    placeholder='Password'
                    className='custom-input'
                    />
              </div>
              <div className='form-submit'>
                  <button
                    className='btn-submit'
                  >
                      LOGIN
                  </button>
              </div>
            
          </form>
          
      </Modal>
    </div>
  )
}

export default Login
