import React from 'react'
import './Navbar.css'
import {BrowserRouter as Router,Link} from 'react-router-dom'
import logo from '../../img/logo.png'
const Navbar = (props) => {
    console.log(props.Lock)
    return (
        <div style={{position:'fixed',width:'100%'}}>
            <ul>
                <li><Link to='/bloggerku'><img src={logo} style={{width:40}}/></Link></li>
                <li><Link to='/about' style={{paddingTop:30}}>About</Link></li>
                <li><Link to='/lastupdate' style={{paddingTop:30}}>Last Update</Link></li>
                {props.Lock == '1'&& <li><Link to='/private' style={{paddingTop:30}}>My-Love</Link></li>}
                {props.Lock == '0'&&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={props.login}>Login</button></li>}
                {props.Lock == '0'&&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={props.register}>Register</button></li>}
                {props.Lock != '0'&&<li><Link to='/write' style={{paddingTop:30}}>WRITE</Link></li>}
            </ul>
        </div>
    )
}

export default Navbar;
